// BASICS
// --------------------- 

.CodeMirror {
  // Set height, width, borders, and global font properties here 
  font-family: @font-family-monospace;
  height: 300px;
}
.CodeMirror-scroll {
  // Set scrolling behaviour here 
  overflow: auto;
}

// PADDING
// --------------------- 

.CodeMirror-lines {
  padding: 4px 0; // Vertical padding around content 
}
.CodeMirror pre {
  padding: 0 4px; // Horizontal padding of content 
}

.CodeMirror-scrollbar-filler,
.CodeMirror-gutter-filler {
  background-color: white; // The little square between H and V scrollbars 
}

// GUTTER
// --------------------- 

.CodeMirror-gutters {
  border-right: 1px solid #ddd;
  background-color: #f7f7f7;
  white-space: nowrap;
}
.CodeMirror-linenumbers {}
.CodeMirror-linenumber {
  padding: 0 3px 0 5px;
  min-width: 20px;
  text-align: right;
  color: #999;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

// CURSOR
// --------------------- 

.CodeMirror div.CodeMirror-cursor {
  border-left: 1px solid black;
}
// Shown when moving in bi-directional text 
.CodeMirror div.CodeMirror-secondarycursor {
  border-left: 1px solid silver;
}
.CodeMirror.cm-keymap-fat-cursor div.CodeMirror-cursor {
  width: auto;
  border: 0;
  background: #7e7;
}
// Can style cursor different in overwrite (non-insert) mode 
div.CodeMirror-overwrite div.CodeMirror-cursor {}

.cm-tab {
  display: inline-block;
}

.CodeMirror-ruler {
  border-left: 1px solid #ccc;
  position: absolute;
}

// DEFAULT THEME
// ---------------------  

.cm-s-default {
  .cm-keyword {color: #708;}
  .cm-atom {color: #219;}
  .cm-number {color: #164;}
  .cm-def {color: #00f;}
  .cm-variable,
  .cm-punctuation,
  .cm-property,
  .cm-operator {}
  .cm-variable-2 {color: #05a;}
  .cm-variable-3 {color: #085;}
  .cm-comment {color: #a50;}
  .cm-string {color: #a11;}
  .cm-string-2 {color: #f50;}
  .cm-meta {color: #555;}
  .cm-qualifier {color: #555;}
  .cm-builtin {color: #30a;}
  .cm-bracket {color: #997;}
  .cm-tag {color: #170;}
  .cm-attribute {color: #00c;}
  .cm-header {color: blue;}
  .cm-quote {color: #090;}
  .cm-hr {color: #999;}
  .cm-link {color: #00c;}
}

// Based on Sublime Text's Monokai theme 

.cm-s-monokai {
  &.CodeMirror {background: #272822; color: #f8f8f2;}
  .CodeMirror-selected {background: #49483E !important;}
  .CodeMirror-gutters {background: #272822; border-right: 0px;}
  .CodeMirror-linenumber {color: #d0d0d0;}
  .CodeMirror-cursor {border-left: 1px solid #f8f8f0 !important;}
  .cm-comment {color: #75715e;}
  .cm-atom {color: #ae81ff;}
  .cm-number {color: #ae81ff;}
  .cm-property,
  .cm-attribute {color: #a6e22e;}
  .cm-keyword {color: #f92672;}
  .cm-string {color: #e6db74;}
  .cm-variable {color: #a6e22e;}
  .cm-variable-2 {color: #9effff;}
  .cm-def {color: #fd971f;}
  .cm-bracket {color: #f8f8f2;}
  .cm-tag {color: #f92672;}
  .cm-link {color: #ae81ff;}
  .cm-error {background: #f92672; color: #f8f8f0;}
  .CodeMirror-activeline-background {background: #373831 !important;}
  .CodeMirror-matchingbracket {
    text-decoration: underline;
    color: white !important;
  }
}

.cm-negative {color: #d44;}
.cm-positive {color: #292;}
.cm-header,
.cm-strong {font-weight: bold;}
.cm-em {font-style: italic;}
.cm-link {text-decoration: underline;}

.cm-s-default .cm-error {color: #f00;}
.cm-invalidchar {color: #f00;}

div.CodeMirror span.CodeMirror-matchingbracket {color: #0f0;}
div.CodeMirror span.CodeMirror-nonmatchingbracket {color: #f22;}
.CodeMirror-activeline-background {background: #e8f2ff;}

// STOP
// ---------------------  

// The rest of this file contains styles related to the mechanics of
// the editor. You probably shouldn't touch them. 

.CodeMirror {
  line-height: 1.2;
  position: relative;
  overflow: hidden;
  background: white;
  color: black;
}

.CodeMirror-scroll {
  // 30px is the magic margin used to hide the element's real scrollbars 
  // See overflow: hidden in .CodeMirror 
  margin-bottom: -30px; margin-right: -30px;
  padding-bottom: 30px;
  height: 100%;
  outline: none; // Prevent dragging from highlighting the element 
  position: relative;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}
.CodeMirror-sizer {
  position: relative;
  border-right: 30px solid transparent;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

// The fake, visible scrollbars. Used to force redraw during scrolling
// before actuall scrolling happens, thus preventing shaking and
// flickering artifacts. 
.CodeMirror-vscrollbar,
.CodeMirror-hscrollbar,
.CodeMirror-scrollbar-filler,
.CodeMirror-gutter-filler {
  position: absolute;
  z-index: 6;
  display: none;
}
.CodeMirror-vscrollbar {
  right: 0; top: 0;
  overflow-x: hidden;
  overflow-y: scroll;
}
.CodeMirror-hscrollbar {
  bottom: 0; left: 0;
  overflow-y: hidden;
  overflow-x: scroll;
}
.CodeMirror-scrollbar-filler {
  right: 0; bottom: 0;
}
.CodeMirror-gutter-filler {
  left: 0; bottom: 0;
}

.CodeMirror-gutters {
  position: absolute; left: 0; top: 0;
  padding-bottom: 30px;
  z-index: 3;
}
.CodeMirror-gutter {
  white-space: normal;
  height: 100%;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  padding-bottom: 30px;
  margin-bottom: -32px;
  display: inline-block;
  // Hack to make IE7 behave 
  *zoom:1;
  *display:inline;
}
.CodeMirror-gutter-elt {
  position: absolute;
  cursor: default;
  z-index: 4;
}

.CodeMirror-lines {
  cursor: text;
}
.CodeMirror pre {
  // Reset some styles that the rest of the page might have set 
  -moz-border-radius: 0; -webkit-border-radius: 0; border-radius: 0;
  border-width: 0;
  background: transparent;
  font-family: inherit;
  font-size: inherit;
  margin: 0;
  white-space: pre;
  word-wrap: normal;
  line-height: inherit;
  color: inherit;
  z-index: 2;
  position: relative;
  overflow: visible;
}
.CodeMirror-wrap pre {
  word-wrap: break-word;
  white-space: pre-wrap;
  word-break: normal;
}

.CodeMirror-linebackground {
  position: absolute;
  left: 0; right: 0; top: 0; bottom: 0;
  z-index: 0;
}

.CodeMirror-linewidget {
  position: relative;
  z-index: 2;
  overflow: auto;
}

.CodeMirror-widget {}

.CodeMirror-wrap .CodeMirror-scroll {
  overflow-x: hidden;
}

.CodeMirror-measure {
  position: absolute;
  width: 100%;
  height: 0;
  overflow: hidden;
  visibility: hidden;
}
.CodeMirror-measure pre { position: static; }

.CodeMirror div.CodeMirror-cursor {
  position: absolute;
  border-right: none;
  width: 0;
}

div.CodeMirror-cursors {
  visibility: hidden;
  position: relative;
  z-index: 1;
}
.CodeMirror-focused div.CodeMirror-cursors {
  visibility: visible;
}

.CodeMirror-selected { background: #d9d9d9; }
.CodeMirror-focused .CodeMirror-selected { background: #d7d4f0; }
.CodeMirror-crosshair { cursor: crosshair; }

.cm-searching {
  background: #ffa;
  background: rgba(255, 255, 0, .4);
}

// IE7 hack to prevent it from returning funny offsetTops on the spans 
.CodeMirror span { *vertical-align: text-bottom; }

// Used to force a border model for a node 
.cm-force-border { padding-right: .1px; }

@media print {
  // Hide the cursor when printing 
  .CodeMirror div.CodeMirror-cursors {
    visibility: hidden;
  }
}
